function AboutPage() {
  return (
    <section className="card">
      <h1 className="title">O autorze</h1>
      <p>
        O autorze, czyli o mnie. Nazywam si Mateusz mam 20lat. Bardzo lubie sycha muzyki, lubie kady rodzaj bo stare dobre hity z lat 80,90-tych po love-songi i brutal deadmetal. Mieszkam w B-stoku i w tym momencie edukuje si na frontend developera. Poza tym bardzo lubie gra w pike i oglda seriale. 
      </p>
    </section>
  );
}

export default AboutPage;
