import { NavLink } from "react-router-dom";

function Navigation() {
  return (
    <nav className="navigation">
      <ul className="navigation__list">
        <li>
          <NavLink
            to="/"
            end
            className={({ isActive }) =>
              isActive ? "navigation__link navigation__link--active" : "navigation__link"
            }
          >
            Zadania
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/about"
            className={({ isActive }) =>
              isActive ? "navigation__link navigation__link--active" : "navigation__link"
            }
          >
            O autorze
          </NavLink>
        </li>
      </ul>
    </nav>
  );
}

export default Navigation;
